.card-enter, .slide-enter {
    background: red;
     -webkit-transition: all 0.5s;
     -o-transition: all 0.5s;
     transition: all 0.5s;
     -webkit-transform: translateX(-120%);
         -ms-transform: translateX(-120%);
             transform: translateX(-120%)
}

.card-enter-active, .slide-enter-active {
    background: yellow;
    max-height: 60px;
    padding: 2rem 0 !important;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}

.card-leave, .slide-leave {
    -webkit-transition:all 0.5s;
    -o-transition:all 0.5s;
    transition:all 0.5s;
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}

.card-leave-active, .slide-leave-active {
    -webkit-transform: translateX(120%);
        -ms-transform: translateX(120%);
            transform: translateX(120%);
}
 .card-stack > .card:hover {
    border: 1px solid lightgray;

    /* outline: 2px solid lightgray;
    outline-offset: 7.5px;
    transition: all .1s ease-in; */

    -webkit-transition: all 0.5s;

    -o-transition: all 0.5s;

    transition: all 0.5s; 
    -webkit-transition-duration: all 0.5s; 
         -o-transition-duration: all 0.5s; 
            transition-duration: all 0.5s;
    -webkit-transform: translateY(-25%);
        -ms-transform: translateY(-25%);
            transform: translateY(-25%);
  }

  .card-stack > .card:active {
    background: rgb(154, 255, 255);
    /* transform: translate(-200px, -500px);
    transition: all 0.5s;
    transition-timing-function: ease-in; */
  } 

  .top-three-card >.card {
    /* transition: all 0.5s;
    transform: translateX(120%); */
  }

  .top-three:active {
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform: translateX(60%);
        -ms-transform: translateX(60%);
            transform: translateX(60%);
    -webkit-transition-timing-function: ease-in;
         -o-transition-timing-function: ease-in;
            transition-timing-function: ease-in;
  }

  .top-three:active {
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform: translateX(60%);
        -ms-transform: translateX(60%);
            transform: translateX(60%);
    -webkit-transition-timing-function: ease-in;
         -o-transition-timing-function: ease-in;
            transition-timing-function: ease-in;
  }


